import React from 'react';
import styled from 'styled-components';
import media from 'utils/styles/media';
import Column from './Column';

export default function Card (props) {

    const widthCorner = 10;

    const Container = styled.div`
        overlow: hidden;
        pointer-events: none;
        background-color:white;
        z-index: 2;
        margin-bottom: 10px;
        
        ${media.tablet`
            opacity: .7;
            background-color:#f2f2f2;
            width: 220px;
            ${propsS => propsS.bottom  ? 'bottom:' + propsS.bottom + '%;' : ''}
            ${propsS => propsS.top ? 'top:' + propsS.top + '%;' : ''}
            position:absolute;
            margin: 0;

            ${Column}:hover & {
                background-color:white;
                opacity: 1;

            }
        `}

        h5 {
            margin: 0 0 10px 0;
        }
        
        p {
            margin: 0;
        }
    `

    const Inner = styled.div`
        padding: 10px;  
    `;

    const Corner = styled.i`
        width: ${widthCorner}px;
        height: ${widthCorner}px;
        border-radius: 50%;
        background-color: #fff;

        ${propsS => (propsS.top || propsS.bottom) ? `margin-top: -${widthCorner / 2}px;` : ``}
        ${propsS => (propsS.bottom) ? `margin-bottom: -${widthCorner / 2}px;` : ``}
        
        ${propsS => propsS.left ? `
            float: left;
            margin-left: -${widthCorner / 2}px;
        ` : ``}

        ${propsS => propsS.right ? `
            float: right;
            margin-right: -${widthCorner / 2}px;
        ` : ``}
    `;


    return (

        <Container {...props}>
            <Inner>
                <i class="top left"></i>
                <i class="top right"></i>
                <div class="content">
                    {props.children}
                </div>
                <i class="bottom right"></i>
                <i class="bottom left"></i>
            </Inner>
        </Container>

    );
}